
.skill_element {

    width: 450px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: wheat;
}

.icon {

    height: 100px;
    width: 90px;
}

.icon:hover {
    box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.50), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
    will-change: transform;
    transform: scale(1.3, 1.3);

}

.title {
    margin-top: 0px;
    margin-bottom: 50px;
}